.FinishPopup {
    width: 100%;
    color: white;
    border: 2px solid white;
    border-radius: 4px;
    max-width: 830px;
    margin: 0 auto;
    padding: 50px;
    max-height: 480px;
    overflow: auto;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}

.FinishPopup__title {
    margin-bottom: 20px;
}

.FinishPopup__list {
    list-style: none;

    margin-bottom: 15px;
}

.FinishPopup__list-item {
    margin-bottom: 5px;
}

.FinishPopup__paragraph {
    margin-bottom: 23px;
}

.FinishPopup__button {
    width: 100px;
    height: 33px;
    border: 1px solid white;
    color: white;
    background: transparent;

    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}