.Question__list-item {
    border: 1px solid white;
    margin-bottom: 10px;
    padding: 10px 15px;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    transition: 0.2s;
    -webkit-transition: 0.2s;
    -moz-transition: 0.2s;
    -ms-transition: 0.2s;
    -o-transition: 0.2s;
}

.Question__list-item:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.274);
}

.Question__list-item--true {
    background-color: rgb(88, 199, 88);
}

.Question__list-item--error {
    background-color: rgb(214, 62, 62);
}
